import {IEndPoint, IO} from "lib/commons/io/io";
import {JLastDatas} from "lib/commons/lastDatas";
import {IRegPointer, REG} from 'lib/commons/registry';
import {ISignboard, Signboard} from "lib/commons/utils/signboard";
import {DOM, JSX} from "lib/commons/xml/dom";
import {EButtonUiContext, IAction} from "lib/commons/actions";
import {BaseElement, BaseElementAsync, MsgLabel, OLabelInit} from "back/commons/basis";
import {IFormJsonisable} from "lib/commons/forms";
import {ActionBtn} from "back/commons/widgets/buttons";

type dynString<C> = (ctx: C, area: Area<C>) => string;
type dynBoolean<C> = (ctx: C, area: Area<C>) => boolean;
type dynAny<C, T = any> = (ctx: C, area: Area<C>) => T;

/**
 * Un Area est une factory d'un widget graphique (view, input...).
 *
 * Chaque Area possède :
 *  - les méta-données du widget (id, label, desc, icon, group),
 *  - la visibilité et les permissions d'affichage du widget,
 *  - les librairies nécessaires à charger en amont de l'instanciation du widget,
 *  - la fonction pour construire le widget.
 */
export interface IArea<C = {}, E extends HTMLElement = HTMLElement> extends ISignboard<C> {

	/** Cette Area exige-t-elle une chargement asynchrone de librairies ? */
	needAsync(ctx: C): boolean

	/**
	 * Construit une zone graphique.
	 * @param ctx Données permettant de construire et initialiser l'area.
	 * @return Node créé
	 */
	buildBody(ctx: C, lastDatas?: JLastDatas): E

	/**
	 * Construit une zone graphique en s'assurant que les libs nécessaires sont chargées.
	 * @param ctx Données permettant de construire et initialiser l'area.
	 * @return Node créé
	 */
	loadBody(ctx: C, lastDatas?: JLastDatas): Promise<E>

	/**
	 * Si needAsync===true force le chargement des libs async sans construire de body.
	 * Une fois la promesse résolue, needAsync devrait être égal à false, sauf si l'impl ne permet
	 * pas ce préchargement.
	 */
	loadLibs(ctx: C): Promise<void>
}

export function isArea<C, E extends HTMLElement>(a: any): a is IArea<C, E> {return a && typeof a.needAsync === "function" && typeof a.buildBody === "function"}

export class Area<C = {}, E extends HTMLElement = HTMLElement> extends Signboard<C> implements IArea<C, E> {

	protected _body: (ctx: C, area: Area<C, E>, lastDatas?: JLastDatas) => E;

	needAsync(ctx: C): boolean {return false}

	buildBody(ctx: C, lastDatas?: JLastDatas): E {
		return this._body(ctx, this, lastDatas);
	}

	async loadBody(ctx: C, lastDatas?: JLastDatas): Promise<E> {
		return this.buildBody(ctx, lastDatas);
	}

	async loadLibs(ctx: C): Promise<void> {}

	setBodyBuilder(body: (ctx: C, area: Area<C>, lastDatas?: JLastDatas) => E): this {
		this._body = body;
		return this;
	}

	/** Surcharge d'une propriété / méthode quelconque. */
	override(name: keyof Area, val: any): this {
		this[name] = val;
		return this;
	}

}

export class AreaAsync<C = {}, E extends HTMLElement = HTMLElement> extends Area<C, E> {

	protected _libsLoadedIn: Window;
	protected _libs: IEndPoint[];
	protected _modules: any[];

	async loadBody(ctx: C, lastDatas?: JLastDatas): Promise<E> {
		if (this._libsLoadedIn !== window) await this.loadLibs(ctx);
		return this.buildBody(ctx, lastDatas);
	}

	needAsync(ctx: C): boolean {
		if (this._libsLoadedIn === window) return false;
		return this._libs?.length > 0;
	}

	/** Préchargement des libs nécessaires. */
	async loadLibs(ctx: C): Promise<void> {
		if (this._libsLoadedIn === window) return;
		if (this._libs?.length) this._modules = await IO.importAllJs(this._libs);
		this._libsLoadedIn = window;
	}

	/**
	 * Chargement asycnhrone des librairies nécessaires à la construction de cet area.
	 */
	requireLib(...libs: IEndPoint[]): this {
		if (libs) {
			if (!this._libs) this._libs = libs;
			else IO.appendUrl(this._libs, ...libs);
		}
		return this;
	}
}


/**
 * Pointeur d'une area avec son contexte.
 * Ces propriétés peuvent être définies sur le widget construit par l'area ou sur son container permettant sa construction.
 * Exemple : pour un onglet d'un c-tabs, la ref à l'area et son contexte peut-être porté par l'objet interne c-tab et/ou
 *        par la view constituant le contenu de l'onglet.
 */
export interface IAreaPointer<C = {}, E extends HTMLElement = HTMLElement> {
	area?: IArea<C, E>;
	areaContext?: C;
}

export function isAreaPointer(h: any): h is IAreaPointer {return h && ((h as IAreaPointer).area != null)}


/** Area dont la construction du widget se résume à l'instanciation d'un simple tag. */
export class SimpleTagArea<C, E extends HTMLElement> extends AreaAsync<C, E> {
	tagName: string;

	constructor(customTag: string, libs?: IEndPoint | IEndPoint[], id?: string) {
		super(id || customTag);
		this.tagName = customTag;
		if (libs) Array.isArray(libs) ? this.requireLib(...libs) : this.requireLib(libs);
	}

	buildBody(ctx: C, lastDatas?: JLastDatas): E {
		const elt = this.newElt(ctx, lastDatas);
		if (!this.isVisible(ctx)) elt.setAttribute('hidden', '');
		return elt;
	}

	protected newElt(ctx: C, lastDatas?: JLastDatas): E {
		return document.createElement(this.tagName) as E;
	}
}

/** Area pour la construction d'une custom element avec initialisation (setInit/getInit) */
export class CustomTagArea<C, E extends BaseElement, EI = C> extends SimpleTagArea<C, E> {
	_init?: any | dynAny<C, EI>;

	buildBody(ctx: C, lastDatas?: JLastDatas): E {
		const elt = super.buildBody(ctx, lastDatas);
		elt.initialize(this.getInit(ctx));
		return elt;
	}

	setInit(ctx: any | dynAny<C, EI>): this {
		this._init = ctx;
		return this;
	}

	getInit(ctx: C): EI {
		if (this._init) {
			if (typeof this._init === 'function') return this._init(ctx);
			else return this._init;
		}
		return ctx as any as EI;
	}

}

/** Area pour la construction d'une custom element avec initialisation asynchrone (setInit/getInit) */
export class CustomAsyncTagArea<C, E extends BaseElementAsync, EI = C> extends SimpleTagArea<C, E> {
	_init?: any | dynAny<C, EI>;

	needAsync(ctx: C): boolean {return true}

	async loadBody(ctx: C, lastDatas?: JLastDatas): Promise<E> {
		const body = await super.loadBody(ctx, lastDatas);
		body.initialize(this.getInit(ctx));
		await body.initializedAsync;
		return body;
	}

	setInit(ctx: any | dynAny<C, EI>): this {
		this._init = ctx;
		return this;
	}

	getInit(ctx: C): EI {
		if (this._init) {
			if (typeof this._init === 'function') return this._init(ctx);
			else return this._init;
		}
		return ctx as any as EI;
	}
}

/**
 * Surcharge d'une area.
 * L'area surchargée peut être initialisée par setOverridenSvc() via le mécanisme de surcharge
 * des svc des extPoints ou appelé explicitement.
 * La méthode getWrappedArea(ctx) peut aussi être surchargée.
 */
export class AreaOver<C, E extends HTMLElement> extends AreaAsync<C, E> implements REG.IOverridingSvc {

	protected _subArea: Area<C, E>;

	/**
	 * Doit renvoyer ActionWrapper.NULL si pas de wrappedAction.
	 */
	getSubArea(ctx: C): Area<C, E> {return this._subArea || AREAS.NULL}

	/** Méthode utilisée par Reg.getSvc() pour affecter le svc surchargé avant de retourner le svc surchargeant. */
	setOverridenSvc(svc: any) {
		this._subArea = svc;
		return this;
	}

	getLabel(ctx: C) {
		return this._label !== undefined ? super.getLabel(ctx) : this.getSubArea(ctx).getLabel(ctx);
	}

	getDescription(ctx: C) {
		return this._description !== undefined ? super.getDescription(ctx) : this.getSubArea(ctx).getDescription(ctx);
	}

	getGroup(ctx: C) {
		return this._group !== undefined ? super.getGroup(ctx) : this.getSubArea(ctx).getGroup(ctx);
	}

	isVisible(ctx: C) {
		if (this._visible !== undefined) return super.isVisible(ctx);
		if (!this.checkObjectRootVisiblePerm(ctx)) return false;
		return this.getSubArea(ctx).isVisible(ctx);
	}

	buildBody(ctx: C, lastDatas?: JLastDatas): E {
		if (this._body) return super.buildBody(ctx, lastDatas);
		return this.getSubArea(ctx).buildBody(ctx, lastDatas);
	}

	async loadLibs(ctx: C): Promise<void> {
		await Promise.all([super.loadLibs(ctx), this._subArea.loadLibs(ctx)]);
	}
}

/**
 * Wrapper d'une area obtenue dynamiquement via un svc d'un extPoints.
 * La méthode setWrappedSvc(svcCd) permet d'initialiser ce wrapper.
 */
export class AreaOverFromSvc<C, E extends HTMLElement> extends AreaOver<C, E> {
	protected _svcCd: string;

	getWrappedSvcCd() {return this._svcCd}

	setWrappedSvcCd(svcCd: string): this {
		this._svcCd = svcCd;
		return this;
	}

	getSubArea(ctx: C): Area<C, E> {
		return REG.getReg(ctx).getSvc<Area<C, E>>(this._svcCd) || AREAS.NULL;
	}
}

/**
 * Interface pour le contexte de construction des contrôle d'un formulaire
 */
export interface IControlLabelContext {
	/**
	 * Si true, enveloppe l'input du label.
	 * TODO permet-on le positionnement du label ?
	 */
	buildControlLabel?: boolean,
}

export interface IControlAreaContext extends IControlLabelContext {}

/**
 * Area du type MsgLabel
 */
export class MsgArea<C> extends Area<C, MsgLabel> {
	constructor(id?: string, public msgInit?: OLabelInit) {
		super(id);
	}

	/** Construction de l'inputArea par défaut. */
	buildBody(ctx: C): MsgLabel {
		const msg = <MsgLabel î={this.msgInit}/> as MsgLabel;
		msg.setAttribute('area-id', this.getId());
		this._iniMsgElement(msg, ctx);
		return msg;
	}

	/** Initialise les comportement MsgLabel du body */
	protected _iniMsgElement(element: HTMLElement, ctx: C) {
		if (!this.isVisible(ctx)) DOM.setHidden(element, true);
		if (!this.isEnabled(ctx)) element.setAttribute('disabled', '');
	}

}

/**
 * Area du type [label Element]
 */
export abstract class LabelArea<C extends IControlLabelContext = IControlLabelContext> extends Area<C, HTMLElement> {

	/** Construction de l'inputArea par défaut. */
	buildBody(ctx: C): HTMLElement {
		const control = this._buildControl(ctx, this.getId());
		const body = ctx.buildControlLabel ? this._buildControlLabel(control, ctx) : control;
		body.setAttribute('area-id', this.getId());
		this._initBodyElement(body, ctx);
		return body;
	}

	/** Initialise les comportement standard du body */
	protected _initBodyElement(element: HTMLElement, ctx: C) {
		if (!this.isVisible(ctx)) element.hidden = true;
		const title = this.getDescription(ctx);
		if (title) element.title = title;
	}

	/**
	 * Initialise les comportement standard d'un contrôle (visible, enabled, required, etc...).
	 * L'initialistion d'un custom doit se faire en surchargeant cette méthode pour bénéfier de l'affectation des attributs
	 */
	protected _initStdControl(element: HTMLElement, ctx: C) {
		if (!this.isEnabled(ctx)) element.setAttribute('disabled', '');
	}

	protected _buildControlLabel(control: HTMLElement, ctx: C, label = this.getLabel(ctx)): HTMLElement {
		return AREAS.buildControlLabel(control, this.getId() + '~' + Math.floor(Math.random() * 100000).toString(36), label);
	}

	abstract _buildControl(ctx: C, name: string): HTMLElement;
}

/**
 * Area async du type [label Element]
 */
export abstract class LabelAsyncArea<C extends IControlLabelContext = IControlLabelContext> extends AreaAsync<C, HTMLElement> {

	/** Construction de l'inputArea par défaut. */
	async loadBody(ctx: C, lastDatas?: JLastDatas): Promise<HTMLElement> {
		const control = await this._loadControl(ctx, this.getId());
		const body = this._buildControlLabel ? this._buildControlLabel(control, ctx) : control;
		body.setAttribute('area-id', this.getId());
		this._initBodyElement(body, ctx);
		return body;
	}

	/** Construction de l'inputArea par défaut. */
	buildBody(ctx: C): HTMLElement {
		const control = this._buildControl(ctx, this.getId());
		const body = ctx.buildControlLabel ? this._buildControlLabel(control, ctx) : control;
		body.setAttribute('area-id', this.getId());
		this._initBodyElement(body, ctx);
		return body;
	}

	/** Initialise les comportement standard du body */
	protected _initBodyElement(element: HTMLElement, ctx: C) {
		if (!this.isVisible(ctx)) DOM.setHidden(element, true);
	}

	/**
	 * Initialise les comportement standard d'un contrôle (visible, enabled, required, etc...).
	 * L'initialistion d'un custom doit se faire en surchargeant cette méthode pour bénéfier de l'affectation des attributs
	 */
	protected _initStdControl(element: HTMLElement, ctx: C) {
		if (!this.isEnabled(ctx)) element.setAttribute('disabled', '');
	}

	protected _buildControlLabel(control: HTMLElement, ctx: C, label = this.getLabel(ctx), desc = this.getDescription(ctx)): HTMLDivElement {
		const elemId = this.getId() + '~' + Math.floor(Math.random() * 100000).toString(36);
		control.id = elemId;
		return <div class="ctrlLbl">
			<label class="lbl" for={elemId} title={desc}>{label}</label>
			{control}
		</div> as HTMLDivElement;
	}

	protected _buildControl?(ctx: C, name: string): HTMLElement;

	abstract _loadControl(ctx: C, name: string): Promise<HTMLElement>;

	needAsync(ctx: C): boolean {
		return !("_buildControl" in this) ? true : super.needAsync(ctx);
	}

}


/**
 * Area regroupant les comportements pour les contrôle d'un formulaire (input, select et textarea)
 */
export abstract class ControlAsyncArea<C extends IControlAreaContext = IControlAreaContext> extends LabelAsyncArea<C> {
	protected _required: boolean | dynBoolean<C>;
	protected _defaultValue: any | dynAny<C>;
	protected _readOnly: any | dynBoolean<C>;

	setRequired(required: boolean | dynBoolean<C>): this {
		this._required = required;
		return this;
	}

	isRequired(ctx: C) {
		if (typeof this._required === 'function' && this._required(ctx, this) === true) return true;
		if (this._required === true) return true;
		return false;
	}

	/**
	 * Valeur par défaut de l'inputArea
	 * @type object
	 */
	setDefaultValue(defaultValue: any | dynAny<C>) {
		this._defaultValue = defaultValue;
		return this;
	}

	getDefaultValue(ctx: C) {
		return (typeof this._defaultValue === 'function') ? this._defaultValue(ctx, this) : this._defaultValue;
	}

	/**
	 * Indique que le champ est protégé, et enverra un évènement
	 * @param protected
	 */
	setReadOnly(locked: boolean | dynBoolean<C>): this {
		this._readOnly = locked;
		return this;
	}

	isReadOnly(ctx: C) {
		if (typeof this._readOnly === 'function' && this._readOnly(ctx, this) === true) return true;
		if (this._readOnly === true) return true;
		return false;
	}

	/**
	 * Initialise les comportement standard d'un contrôle (visible, enabled, required, etc...).
	 * L'initialistion d'un custom doit se faire en surchargeant cette méthode pour bénéfier de l'affectation des attributs
	 */
	protected async _initStdControl(input: HTMLElement, ctx: C) {
		if (!this.isVisible(ctx)) input.setAttribute('hidden', '');
		if (!this.isEnabled(ctx)) input.setAttribute('disabled', '');
		if (this.isRequired(ctx)) input.setAttribute('required', '');
		if (this.isReadOnly(ctx)) input.setAttribute('readonly', '');

	}
}

/**
 * Area abstraite pour les contrôles acceptant un dataset
 */
export abstract class DatasetAsyncArea<E extends DatasetEntry<C>, C> extends ControlAsyncArea<C> {
	_dataset: Promise<Dataset<E>> | dynAny<C, Promise<Dataset<E>>>;

	setDataset(dataset: Promise<Dataset<E>> | dynAny<C, Promise<Dataset<E>>>): this {
		this._dataset = dataset;
		return this;
	}

	getDataset(ctx: C): Promise<Dataset<E>> {
		return (typeof this._dataset === 'function') ? this._dataset(ctx, this) : this._dataset;
	}
}

/**
 * Area construisant un élément <select> à partir d'un dataset
 */
export class SelectAsyncArea<E extends DatasetEntry<C>, C> extends DatasetAsyncArea<E, C> {
	_multiple: boolean | dynBoolean<C>;

	setMultiple(multiple: boolean | dynBoolean<C>): this {
		this._multiple = multiple;
		return this;
	}

	isMultiple(ctx: C) {
		if (typeof this._multiple === 'function' && this._multiple(ctx, this) === true) return true;
		if (this._multiple === true) return true;
		return false;
	}

	async _loadControl(ctx: C, name: string): Promise<HTMLElement> {
		const selectElt = <select name={name}/> as HTMLSelectElement;
		selectElt.multiple = this.isMultiple(ctx);
		const dataset = await this.getDataset(ctx);
		let defaultValue = this.getDefaultValue(ctx);
		if (!Array.isArray(defaultValue)) {
			if (defaultValue == null) defaultValue = [];
			else defaultValue = [defaultValue];
		}
		for (const key in dataset) {
			const data = dataset[key];
			const option = selectElt.appendChild(<option value={key}>{data.label}</option>) as HTMLOptionElement;
			option.selected = defaultValue.includes(key);
			option.disabled = !this.isEnabled(ctx) || (typeof data.disabled === 'function' ? data.disabled(ctx, this) : data.disabled);
			let desc = (typeof data.description === 'function' ? data.description(ctx, this) : data.description || "");
			if (desc) option.title = desc;
		}
		this._initStdControl(selectElt, ctx);
		return selectElt;
	}
}


/**
 * Area regroupant les comportements pour les contrôle d'un formulaire (input, select et textarea)
 */
export abstract class ControlArea<C extends IControlAreaContext = IControlAreaContext> extends LabelArea<C> {
	protected _required: boolean | dynBoolean<C>;
	protected _defaultValue: any | dynAny<C>;
	protected _locked: any | dynBoolean<C>;

	setRequired(required: boolean | dynBoolean<C>): this {
		this._required = required;
		return this;
	}

	isRequired(ctx: C) {
		if (typeof this._required === 'function' && this._required(ctx, this) === true) return true;
		if (this._required === true) return true;
		return false;
	}

	/**
	 * Valeur par défaut de l'inputArea
	 * @type object
	 */
	setDefaultValue(defaultValue: any | dynAny<C>) {
		this._defaultValue = defaultValue;
		return this;
	}

	getDefaultValue(ctx: C) {
		return (typeof this._defaultValue === 'function') ? this._defaultValue(ctx, this) : this._defaultValue;
	}

	/**
	 * Indique que le champ est protégé, et enverra un évènement
	 * @param protected
	 */
	setReadOnly(locked: boolean | dynBoolean<C>): this {
		this._locked = locked;
		return this;
	}

	isReadOnly(ctx: C) {
		if (typeof this._locked === 'function' && this._locked(ctx, this) === true) return true;
		if (this._locked === true) return true;
		return false;
	}

	/** Initialise les comportement standard d'un contrôle (visible, enabled, required, etc...) */
	protected _initStdControl(input: HTMLElement, ctx: C) {
		if (!this.isVisible(ctx)) input.setAttribute('hidden', '');
		if (!this.isEnabled(ctx)) input.setAttribute('disabled', '');
		if (this.isRequired(ctx)) input.setAttribute('required', '');
		if (this.isReadOnly(ctx)) input.setAttribute('readonly', '');
	}
}

